let calendarHour = getComputedStyle(document.body).getPropertyValue(
  "--calendar-hour"
);
calendarHour = parseInt(calendarHour);

window.addEventListener("load", () => {
  requestIdleCallback(() => {
    import("./../node_modules/hammerjs/hammer.min.js").then(() => {
      const calendar = $(".calendar");

      Hammer.defaults.touchAction = "pan-y";
      var hm = new Hammer.Manager(calendar);

      const pinch = new Hammer.Pinch();

      hm.add([pinch]);
      console.log(pinch);
      hm.on("pinch", function(ev) {
        console.log(ev);
        if (ev.scale > 1) {
          calendarHour += Math.round(ev.scale / 2);
          document.documentElement.style.setProperty(
            "--calendar-hour",
            `${calendarHour}px`
          );
        } else if (ev.scale < 1) {
          calendarHour -= Math.round(ev.scale);
          document.documentElement.style.setProperty(
            "--calendar-hour",
            `${calendarHour}px`
          );
        }
      });
    });
  });
});
